<!---->
<template>
    <el-container direction="vertical"
                  class="demo-shadow">

      <el-header style="height:auto;" >
        <el-row class="box-header" type="flex" justify="space-between">

          <el-col span="6">
            <a></a>
          </el-col>

          <el-col span="6">
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-plus" @click="dialogCommitTitle = ''; dialogFormVisible = true;">Key</el-button>
          </el-col>

        </el-row>
      </el-header>
      <el-divider ></el-divider>

      <el-table :data="tableData" v-show="tableData.length > 0">
        <el-table-column prop="kv_name" label="" width="200" />
        <el-table-column prop="kv_key" label="key" width="300" />
        <el-table-column prop="kv_value" label="" show-overflow-tooltip="true" width="700"/>
        <el-table-column prop="kv_status" label="" width="100">
          <template #default="scope">
            <el-switch v-model="scope.row.kv_status"  active-color="#13ce66" @click="setKvStatus(scope.$index, scope.row)"/>
          </template>
        </el-table-column>

<!--        -->
       <el-table-column fixed="right" label="" min-width="250" align="right">
          <template #default="scope">
            <el-button class="el-button-right" type="primary" size="mini" @click="editKv(scope.$index, scope.row)" icon="el-icon-edit"></el-button>
            <el-button class="el-button-right" type="danger" size="mini" @click="deleteKv(scope.$index, scope.row)" icon="el-icon-close"></el-button>
          </template>
        </el-table-column>
      </el-table>

     <el-empty description="" v-show="tableData.length == 0" style="margin-top: 100px">
       <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-plus" @click=" dialogCommitTitle = ''; dialogFormVisible = true;">Key</el-button>
     </el-empty>

      <!---->
      <el-dialog v-model="dialogFormVisible" title="Key">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth" required="true">
            <el-input v-model="form.kv_name" autocomplete="off"
                      placeholder=""
                      maxlength="24"
                      show-word-limit="true"
            ></el-input>
          </el-form-item>
          <el-form-item label="key" :label-width="formLabelWidth" required="true">
            <el-input v-model="form.kv_key" autocomplete="off"
                      placeholder="key"
                      maxlength="24"
                      show-word-limit="true"
                      :disabled="dialogCommitTitle==``"
            ></el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" required="true">
            <el-input v-model="form.kv_value"
                      autocomplete="off"
                      minlength="0"
                      maxlength="5000"
                      show-word-limit="true"
                      placeholder="key"
                      clearable="true">
            </el-input>
          </el-form-item>

          <el-form-item label="" :label-width="formLabelWidth" >
            <el-row  type="flex" justify="space-between">
              <el-col span="6">
                <el-switch v-model="form.kv_status" active-color="#13ce66" />
              </el-col>
              <el-col span="6"/>
            </el-row>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogFormVisible = false"></el-button>
            <el-button type="primary" @click="addOrUpdateKv">{{dialogCommitTitle}}</el-button
            >
          </span>
        </template>
      </el-dialog>

    </el-container>

</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
export default defineComponent({
  created() {
    this.getKvs()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({
      tableData: [],
      dialogFormVisible: false,
      form: {
        kv_name: '',
        kv_key: '',
        kv_value: '',
        kv_status: true,
      },
      formLabelWidth: '120px',
      dialogCommitTitle: '',
    })

    // / 
    const addOrUpdateKv = () => {
      let reg = /^[A-Za-z0-9_-]+$/
      if(state.form.kv_key.length <= 0 || !reg.test(state.form.kv_key)){
        ElMessage({
                showClose: true,
                message: "kv_key  -_",
                type: 'error',
              })
        return
      }
      if(state.form.kv_value.length <= 0){
        ElMessage({
                showClose: true,
                message: "",
                type: 'error',
              })
        return
      }
      state.dialogFormVisible = false
      console.log(JSON.stringify(state.form))
      axios.post('http://127.0.0.1:8000/api/add_kv', JSON.stringify(state.form))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    const getKvs = () => {
      axios.get('http://127.0.0.1:8000/api/get_kvs')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getKvs Succeed.',
                type: 'success',
              })

              // 
              state.tableData = res.data
            } else {
              ElMessage({
                showClose: true,
                message: 'getKvs Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const editKv = (index: any, row: any) => {
      console.log(index, row)
      state.form = {
        kv_name: row.kv_name,
        kv_key: row.kv_key,
        kv_value: row.kv_value,
        kv_status: row.kv_status
      }
      state.dialogCommitTitle = ""
      state.dialogFormVisible = true
    }

    // 
    const deleteKv = (index: any, row: any) => {
       console.log(row)
       axios.post('http://127.0.0.1:8000/api/del_kv', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    // master
    const setKvStatus = (index: any, row: any) => {
      console.log(row)
       axios.post('http://127.0.0.1:8000/api/kv_status', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    return {
      ...toRefs(state),
      addOrUpdateKv,
      getKvs,
      editKv,
      setKvStatus,
      deleteKv,
    }
  },
})

</script>

<style scoped>

.el-button-right{
  min-height: 25px;
  width: 70px;
}

.el-table{
  margin-left: 32px;
  margin-right: 32px;
  width: auto;
}

.demo-shadow {
  height: auto;
  border: 1px solid var(--el-border-color-base);
  margin: 16px;
  box-shadow: 0 12px 14px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04)
}

.box-header{
  height: 60px;
  align-items: center;
}

.box-header a{
}

.el-button-add{
  height: 20px;
  width: 100px;
}

.el-divider{
  margin-top: 0px;
  margin-bottom: 16px;
}

