            >
              <el-option
                v-for="item in umKeys"
                :key="item.um_key"
                :label="item.um_name"
                :value="item.um_key"
              >
              </el-option>
            </el-select>

            <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-refresh" @click="query.refresh = 1; query.pg_index = 1;getUmEvents()"></el-button>
            <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-delete" @click="doStop"></el-button>
            <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-upload2" @click="dialogFormVisible = true;"></el-button>
            <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-download" @click="exportEvents"></el-button>
            <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-sort" @click="showDrawer = true;"></el-button>
          </el-col>

        </el-row>
      </el-header>

<!--      -->
      <el-divider ></el-divider>

<!--      -->
      <el-table
          :data="tableData"
          v-show="tableData.length > 0"
      >
        <el-table-column type="selection"/>
        <el-table-column type="index" />
        <el-table-column prop="um_name" sortable label="id" width="300" />
        <el-table-column prop="um_displayName" sortable label="" width="300" />
        <el-table-column prop="um_countYesterday" sortable label="" width="130" />
        <el-table-column prop="um_countToday" sortable label="" width="130" />
        <el-table-column prop="um_deviceYesterday" sortable label="" />
      </el-table>

<!--       background-->
     <el-pagination
                    layout="sizes, prev, pager, next, jumper"
                    :current-page="query.pg_index"
                    :page-size="query.pg_size"
                    :page-sizes="[15, 30, 50, 120, 200, 300, 400, 500, 600, 700, 800]"
                    :total="total"
                    prev-text=""
                    next-text=""
                    @current-change="onCurrentPageChange"
                    @size-change="onPageSizeChange"
                    v-show="tableData.length > 0"
                    style="margin: 30px"
     >
     </el-pagination>

<!--      -->
     <el-empty description="" v-show="tableData.length == 0" style="margin-top: 100px">
       <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-upload" @click="dialogFormVisible = true;"></el-button>
     </el-empty>

      <!---->
      <el-dialog v-model="dialogFormVisible" title="">
          <el-upload
              ref="upload"
              action="http://127.0.0.1:8000/api/um_event_import"
              :on-preview="handlePreview"
              :on-success="handleUploadSucceed"
              :file-list="fileList"
              :show-file-list="false"
              multiple="false"
              drag="true"
              :data="query.um_key"
            >
            <el-button size="mini" icon="el-icon-upload" style="background-color: transparent; border: 0px"></el-button>
            <div class="el-upload__text">
               <em>  </em>
            </div>
          </el-upload>
      </el-dialog>

<!--      -->
      <el-drawer
        v-model="showDrawer"
        title=""
        direction="rtl"
        :before-close="onFilterSubmit"
      >

        <el-form ref="form"
                 :model="filterForm"
                 label-width="120px"
                 label-position="right"
                 style="margin-right: 40px; margin-top: 30px"
        >

          <el-form-item label="">
            <el-input v-model="filterForm.keyword"></el-input>
          </el-form-item>

          <el-form-item label="" >
            <el-col>
              <el-select v-model="filterForm.state" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="normal"></el-option>
                <el-option label="" value="stopped"></el-option>
              </el-select>
            </el-col>
          </el-form-item>

          <el-form-item label="" >
            <el-col>
              <el-select v-model="filterForm.type" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-col>
          </el-form-item>

          <el-form-item label="">
            <el-col>
              <el-select v-model="filterForm.sort_by" placeholder="">
                <el-option label="id" value="name"></el-option>
                <el-option label="" value="displayName"></el-option>
                <el-option label="" value="countYesterday"></el-option>
                <el-option label="" value="countToday"></el-option>
                <el-option label="" value="deviceYesterday"></el-option>
              </el-select>
            </el-col>
          </el-form-item>

          <el-form-item label="">
            <el-col>
              <el-select v-model="filterForm.sort" placeholder="">
                <el-option label="" value="desc"></el-option>
                <el-option label="" value="asc"></el-option>
              </el-select>
            </el-col>
          </el-form-item>

          <el-form-item>
            <el-button class="el-button-filter" @click="onFilterReset"></el-button>
            <el-button class="el-button-filter" type="primary" @click="onFilterSubmit"></el-button>
          </el-form-item>

        </el-form>

      </el-drawer>

    </el-container>

</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
import {saveAs} from "file-saver";
export default defineComponent({
  created() {
    this.setDefaultFilterFrom()
    this.getUmKeys()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({

      filterForm: {},

      showDrawer: false,

      umKeys:[],

      fileList:[],

      tableData: [],
      dialogFormVisible: false,
      form: {
        um_key: '',
        um_eventId: '',
        um_name: '',
        um_displayName: '',
        um_status: '',
        um_eventType: '',
        um_eventType_int: 0, //multiattribute=0 ;  calculation=1
        um_countToday: 0,
        um_countYesterday: 0,
        um_deviceYesterday: 0,
      },
      query: {
        um_key: '',
        refresh: 0,
        pg_index: 1,
        pg_size: 15,
      },
      total: 50,
      formLabelWidth: '120px',
      dialogCommitTitle: '',
    })

    // view
    const setDefaultFilterFrom =()=>{
      state.filterForm = {
        keyword: '',
        sort_by: 'deviceYesterday',
        sort: 'desc',
        state: '',
        type: '1',
      }
    }

    // key
    const getUmKeys = () => {
      axios.get('http://127.0.0.1:8000/api/get_um_keys')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              // 
              if(res.data.length > 0){
                state.umKeys = res.data
                state.query.um_key = res.data[0].um_key
                getUmEvents()
              }
            } else {
              ElMessage({
                showClose: true,
                message: 'getUmKeys Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const getUmEvents = () => {
      axios.post('http://127.0.0.1:8000/api/um_event', JSON.stringify(state.query))
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Succeed.',
                type: 'success',
              })

              // 
              state.tableData = res.data.lst
              state.total = res.data.total
              state.query.refresh = 0

            } else {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const editHost = (index: any, row: any) => {
      console.log(index, row)
      state.form = {
        um_key: row.um_key,
        um_eventId: row.um_eventId,
        um_name: row.um_name,
        um_displayName: row.um_displayName,
        um_status: row.um_status,
        um_eventType: row.um_eventType,
        um_eventType_int: row.um_eventType_int,
        um_countToday: row.um_countToday,
        um_countYesterday: row.um_countYesterday,
        um_deviceYesterday: row.um_deviceYesterday,
      }
      state.dialogFormVisible = true
      state.dialogCommitTitle = ""
    }

    // 
    const deleteHost = (index: any, row: any) => {
      console.log(index, row)
    }

    // 
    const doStop = () => {
      console.log("")
    }

    // 
    const addOrUpdateEvent = () => {
      console.log("")
    }

    // 
    const importEvents = () => {
      console.log("")
    }

    // 
    const exportEvents = () => {
      axios.post('http://127.0.0.1:8000/api/um_event_export', JSON.stringify(state.query))
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              // 
              let str = new Blob([res.data], {type: 'text/plain;charset=utf-8'});
              // 
              saveAs(str, `_${state.query.um_key}.txt`);
            } else {
              ElMessage({
                showClose: true,
                message: 'getUmEvents Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const onCurrentPageChange = (index: any) => {
      console.log(`onCurrentPageChange ${index}`)
      state.query.pg_index = index
      getUmEvents()
    }

    // size
    const onPageSizeChange = (size: any) => {
      console.log(`onPageSizeChange ${size}`)
      state.query.pg_index = 1
      state.query.pg_size = size
      getUmEvents()
    }

    // 
    const handlePreview = (file: any) => {
      console.log(`handlePreview ${file}`)
    }

    // 
    const handleUploadSucceed = (response: any, file: any, file_list: any) => {
      state.dialogFormVisible = false
      console.log(`handleUploadSucceed ${response }${file} ${file_list}`)
      ElMessage({
                showClose: true,
                message: '',
                type: 'success',
              })
    }

    // key
    const onUmKeyChange = (um_key: any) => {
      console.log(`onUmKeyChange ${um_key}`)
      state.query.pg_index = 1
      getUmEvents()
    }

    // view
    const onFilterReset = () => {
      console.log(`onFilterReset`)
      setDefaultFilterFrom()
    }

    // view
    const onFilterSubmit = () => {
      console.log(`onFilterSubmit ${state.filterForm}`)
      console.log(state.filterForm)
      state.showDrawer = false
    }

    return {
      ...toRefs(state),
      getUmKeys,
      getUmEvents,

      editHost,
      deleteHost,
      doStop,
      addOrUpdateEvent,
      onCurrentPageChange,
      onPageSizeChange,
      exportEvents,
      importEvents,

      handlePreview,
      handleUploadSucceed,

      onUmKeyChange,

      onFilterSubmit,
      onFilterReset,
      setDefaultFilterFrom,
    }
  },
})

</script>

