<!---->
<template>
<el-container direction="vertical"
                  class="demo-shadow">

      <el-header style="height:auto;" >
        <el-row class="box-header" type="flex" justify="space-between">

          <el-col span="6">
            <a></a>
          </el-col>

          <el-col span="6">
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-close" v-if="false" @click="timeLineList=[]"></el-button>
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-upload2" @click="initWebsocket">sock</el-button>
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-close" v-if="false" @click="stopTask"></el-button>
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-refresh" @click="dialogFormVisible = true;">/</el-button>
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-phone" @click="startSynTask"></el-button>
          </el-col>

        </el-row>
      </el-header>


  <!---->
      <el-divider ></el-divider>


  <!-- -->
    <el-timeline>
      <el-timeline-item
          style="text-align:left"
        v-for="(item, index) in timeLineList"
        :key="index"
        :timestamp="item.timestamp"
        :center="false"
      >
        {{ item.content }}
      </el-timeline-item>
    </el-timeline>


    <!---->
    <el-dialog v-model="dialogFormVisible" title="">
        <el-upload
            ref="upload"
            action="http://127.0.0.1:8000/api/um_event_update"
            :on-success="handleUploadSucceed"
            :file-list="fileList"
            :show-file-list="false"
            multiple="false"
            drag="true"
            :data="umConfig.UM_KEY_MASTER"
          >
          <el-button size="mini" icon="el-icon-upload" style="background-color: transparent; border: 0px"></el-button>
          <div class="el-upload__text">
             <em>  </em>
          </div>
        </el-upload>
    </el-dialog>


  </el-container>

</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
import {Socket} from "socket.io-client/build/esm/socket";

let socketClient: Socket|any = null;
let timer: Socket|any = null;

export default defineComponent({

  components: {
  },

  created() {
    // 
    // this.initWebsocket()
    this.getConfig()
  },

  // onMounted(){
  //   // this.initWebsocket()
  // },

  beforeUnmount() {
    this.closeWebsocket()
    //  websocket 
    socketClient = null
  },

  setup() {
    const axios = require('axios');

    const state = reactive( {
      // 
      umConfig: {
        CONTENT_TYPE: '',
        USER_AGENT: '',
        X_XSRF_TOKEN: '',
        X_XSRF_HAITANG: '',
        COOKIE: '',
        UM_KEY_MASTER: '',
        UM_KEY_SLAVES: [],
      },
      // 
      taskType: '',
      // 
      timeLineList: [
        {
          content: '',
          timestamp: "",
        },
        {
          content: '',
          timestamp: "",
        },
      ],

      dialogFormVisible: false,
      fileList:[],
    })

    /**
     * 
     */
    const getCurrDate = () => {
      // , 
      return new Date().toDateString()
    }

    /**
     * 
     */
    const stopTask = () => {
      state.taskType = ''
      websocketSend({'type': 'stop', 'config': state.umConfig})
    }

    /**
     *  
     */
    const startSynTask = () => {
      state.taskType = 'syn'
      startTask(state.taskType)
    }

    /**
     *  /
     */
    const startUpdateTask = () => {
      state.taskType = 'update'
      startTask(state.taskType)
    }

    /**
     * 
     */
    const startTask = (taskType: string) => {
      // 
      state.timeLineList = []
      state.taskType = taskType

      // websocket, 
      if(initWebsocket()){
        let data = {
          'type': taskType,
          'config': state.umConfig
        }
        websocketSend(data)
      }
    }

    /**
     * websocket
     * @param data
     */
    const websocketOnMessage = (msg: any) => {
        console.log('websocketOnMessage date=', msg);
        let data: string = msg.data
        state.timeLineList.push({
            content: data,
            timestamp: getCurrDate(),
          })

        if ('' == data){
          state.taskType = ''
        }
    }
    
    /**
     * websocket
     */
    const websocketOnOpen = () => {
        console.log('websocketOnOpen');
        websocketSend({'tip': '', 'config': state.umConfig})
        if(state.taskType != null && state.taskType != ''){
          let data = {
            'type': state.taskType,
            'config': state.umConfig
          }
          websocketSend(data)
        }
    }

    /**
     * websocket
     */
    const websocketOnConnect = () => {
        console.log('websocketOnConnect');
    }

    /**
     * websocket
     */
    const websocketOnError = () => {
        console.log('websocketOnError');
        initWebsocket();
    }

    /**
     * websocket
     * @param e
     */
    const websocketOnClose = (e: any) => {
        console.log('websocketOnClose  e=',e);
        // closeWebsocket()
    }

    /**
     * websocket
     * @param data
     */
    const websocketSend = (data: any) => {
        console.log('websocketSend data=',data);
        if(socketClient != null && data != null){
          socketClient.send(JSON.stringify(data));
        }
    }


    /**
     * websocket
     */
    const initWebsocket = () => {
      socketClient = new WebSocket("ws://127.0.0.1:8000/ws/um");
      socketClient.onmessage = websocketOnMessage;
      socketClient.onopen = websocketOnOpen;
      socketClient.onconnect = websocketOnConnect;
      socketClient.onerror = websocketOnError;
      socketClient.onclose = websocketOnClose;
      return true
    }

    /**
     * websocket
     */
    const closeWebsocket =()=>{
      // 
      if(socketClient != null){
        socketClient.close()
      }
      // socketClient = null
      console.log(' websocket ');
    }

    /**
     * 
     */
    const startTimer =()=>{
      timer = setInterval(()=>{
        console.log(1)
      },1000)
    }
    /**
     * 
     */
    const stopTimer =()=>{
      clearInterval(timer)
    }

    /**
     * 
     */
    const getConfig = () => {
      axios.get('http://127.0.0.1:8000/api/get_config')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getConfig Succeed.',
                type: 'success',
              })

              state.umConfig = res.data

            } else {
              ElMessage({
                showClose: true,
                message: 'getConfig Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const handleUploadSucceed = (response: any, file: any, file_list: any) => {
      state.dialogFormVisible = false
      console.log(`handleUploadSucceed ${response }${file} ${file_list}`)
      ElMessage({
                showClose: true,
                message: '',
                type: 'success',
              })
      startUpdateTask()
    }

    return {
      ...toRefs(state),
      stopTask,
      startTask,
      startSynTask,
      startUpdateTask,

      initWebsocket,
      closeWebsocket,

      websocketOnMessage,
      websocketOnOpen,
      websocketOnConnect,
      websocketOnError,
      websocketOnClose,
      websocketSend,

      getCurrDate,
      getConfig,

      startTimer,
      stopTimer,

      handleUploadSucceed,
    }
  },
})

</script>

<style scoped>

.el-button-right{
  min-height: 25px;
  width: 70px;
}

.el-table{
  margin-left: 32px;
  margin-right: 32px;
