<!---->
<template>
    <el-container direction="vertical"
                  class="demo-shadow">

      <el-header style="height:auto;" >
        <el-row class="box-header" type="flex" justify="space-between">

          <el-col span="6">
            <a>key</a>
          </el-col>

          <el-col span="6">
           <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-plus" @click="dialogFormVisible = true; dialogCommitTitle = ''">Key</el-button>
          </el-col>

        </el-row>
      </el-header>

      <el-divider ></el-divider>

      <el-table :data="tableData" v-show="tableData.length > 0">
        <el-table-column prop="um_name" label="" width="300" />
        <el-table-column prop="um_key" label="key" width="500" />
        <el-table-column prop="um_master" label="Master" >
          <template #default="scope">
            <el-switch v-model="scope.row.um_master"  active-color="#13ce66" @click="setMaster(scope.$index, scope.row)"/>
          </template>
        </el-table-column>

<!--        -->
       <el-table-column fixed="right" label="" min-width="280" align="right">
          <template #default="scope">
            <el-button class="el-button-right" type="primary" size="mini" @click="editHost(scope.$index, scope.row)" icon="el-icon-edit"></el-button>
            <el-button class="el-button-right" type="danger" size="mini" @click="deleteHost(scope.$index, scope.row)" icon="el-icon-close"></el-button>
          </template>
        </el-table-column>
      </el-table>

     <el-empty description="" v-show="tableData.length == 0" style="margin-top: 100px">
       <el-button size="mini" class="el-button-add" type="primary" icon="el-icon-plus" @click="dialogFormVisible = true; dialogCommitTitle = ''">Key</el-button>
     </el-empty>

      <!---->
      <el-dialog v-model="dialogFormVisible" title="Key">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.um_name" autocomplete="off"
                      placeholder="key"
                      maxlength="24"
                      show-word-limit="true"
            ></el-input>
          </el-form-item>

          <el-form-item label="key" :label-width="formLabelWidth" required="true">
            <el-input v-model="form.um_key"
                      autocomplete="off"
                      minlength="24"
                      maxlength="24"
                      show-word-limit="true"
                      placeholder="24key"
                      clearable="true"
                      :disabled="dialogCommitTitle==``"
            >
            </el-input>
          </el-form-item>

          <el-form-item label="Master" :label-width="formLabelWidth" >
            <el-row  type="flex" justify="space-between">
              <el-col span="6">
                <el-switch v-model="form.um_master" active-color="#13ce66" />
              </el-col>
              <el-col span="6"/>
            </el-row>
          </el-form-item>
        </el-form>
        <template #footer>
          <span class="dialog-footer">
            <el-button @click="dialogFormVisible = false"></el-button>
            <el-button type="primary" @click="addOrUpdateKey">{{dialogCommitTitle}}</el-button
            >
          </span>
        </template>
      </el-dialog>

    </el-container>

</template>

<script lang="ts">
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
export default defineComponent({
  created() {
    this.getUmKeys()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({
      tableData: [],
      dialogFormVisible: false,
      form: {
        um_name: '',
        um_key: '',
        um_master: false,
      },
      formLabelWidth: '120px',
      dialogCommitTitle: '',
    })

    // / 
    const addOrUpdateKey = () => {
      let reg = /^[A-Za-z0-9]+$/
      if(state.form.um_key.length != 24 || !reg.test(state.form.um_key)){
        ElMessage({
                showClose: true,
                message: "24key",
                type: 'error',
              })
        return
      }
      state.dialogFormVisible = false
      console.log(JSON.stringify(state.form))
      axios.post('http://127.0.0.1:8000/api/add_um_key', JSON.stringify(state.form))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    const getUmKeys = () => {
      axios.get('http://127.0.0.1:8000/api/get_um_keys')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getUmKeys Succeed.',
                type: 'success',
              })

              // 
              state.tableData = res.data
            } else {
              ElMessage({
                showClose: true,
                message: 'getUmKeys Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const editHost = (index: any, row: any) => {
      console.log(index, row)
      state.form = {
        um_name: row.um_name,
        um_key: row.um_key,
        um_master: row.um_master
      }
      state.dialogFormVisible = true
      state.dialogCommitTitle = ""
    }

    // 
    const deleteHost = (index: any, row: any) => {
       console.log(row)
       axios.post('http://127.0.0.1:8000/api/del_um_key', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    // master
    const setMaster = (index: any, row: any) => {
      console.log(row)
       axios.post('http://127.0.0.1:8000/api/um_key_master', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    return {
      ...toRefs(state),
      addOrUpdateKey,
      getUmKeys,
      editHost,
      setMaster,
      deleteHost,
    }
  },
})

</script>

<style scoped>

.el-button-right{
  min-height: 25px;
  width: 70px;
}

.el-table{
  margin-left: 32px;
  margin-right: 32px;
  width: auto;
}

.demo-shadow {
  height: auto;
  border: 1px solid var(--el-border-color-base);
  margin: 16px;
  box-shadow: 0 12px 14px rgba(0, 0, 0, 0.12), 0 0 6px rgba(0, 0, 0, 0.04)
}

.box-header{
  height: 60px;
  align-items: center;
}

.box-header a{
}

.el-button-add{
  height: 20px;
  width: 100px;
}

.el-divider{
  margin-top: 0px;
  margin-bottom: 16px;
}

</style>